html {
	height: 100%;
}

body {
	width: 100%;
	min-width: $desktop-min-width;
	background-color: $white;
	margin: 0 auto;
	text-align: center;
	font-family: "Arial", "Helvetica", sans-serif;
	font-size: 18px;
	line-height: 150%;
	font-style: normal;
	color: $black;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-x: hidden;
	position: relative;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
	white-space: nowrap;
	clip-path: inset(100%);
}

@media (max-width: $tablet-max-width) {
	body {
		min-width: $tablet-min-width;
	}
}

@media (max-width: $mobile-max-width) {
	body {
		min-width: $mobile-min-width;
		font-size: 16px;
	}
}
