.products {
    &__list-container {
        margin: 48px 0 0;
    }

    &__list {
        width: 100%;
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        list-style: none;
        color: $white;
        text-align: left;
    }

    &__item {
        width: 283px;
        height: 382px;
        position: relative;
    }

    &__item-link {
        text-decoration: none;
        color: $white;
    }

    &__item-background {
        width: 283px;
        height: 382px;
    }

    &__description {
        position: absolute;
        top: 0;
        left: 0;
        box-sizing: border-box;
        height: 100%;
        padding: 20px 16px 16px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        h3 {
            font-family: $Stratos;
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            line-height: 119%;
            margin: 0;
        }

        p {
            margin: 0;
            width: 200px;
            font-weight: bold;
            font-size: 17px;
            line-height: 20px;
        }
    }

    &__discount {
        width: 86px;
        height: 86px;
        position: absolute;
        bottom: 20px;
        right: -20px;
    }

    &__links {
        span {
            display: block;
            color: $white;
            text-decoration: none;
            font-size: 0.67em;
            line-height: 125%;
        }
    }

}

@media (max-width: $tablet-max-width) {
    .products {
        &__list-container {
            width: 100%;
            max-width: $tablet-max-width;
            box-sizing: border-box;
            padding: 0 16px;
            margin: 32px 0 0;
            position: relative;

            overflow: auto;
            scrollbar-width: none;
            -ms-overflow-style: none;

            &::-webkit-scrollbar {
                display: none;
            }

            &::after {
                position: absolute;
                right: 0;
                top: 0;
                display: inline-block;
                content: "";
                width: 608px;
                height: 382px;
                background-image: url("../img/gradient.png");
                background-repeat: no-repeat;
                background-position: right;
                cursor: pointer;
                transition: top 0.5s;
                pointer-events: none;
            }

            &--no-mask {
                &::after {
                    display: none;
                }
            }
        }

        &__list {
            transition-property: transform;
        }

        &__item:not(:last-child) {
            margin: 0 29px 0 0;
        }

        &__list-pagination {
            margin: 24px auto 0;
            width: max-content;
            max-width: $tablet-max-width;
            display: flex;
            justify-content: flex-start;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .products {
        &__list-container {
            max-width: $mobile-max-width;
            margin: 24px 0 0;
            padding: 0;
        }

        &__list {
            flex-direction: column;
            margin: 0 auto;
            width: 288px;
        }

        &__item {
            width: 288px;
            height: 382px;

            &:not(:last-child) {
                margin: 0 0 16px 0;
            }
        }

        &__discount {
            right: -6px;
        }

        &__list-pagination {
            display: none;
        }
    }
}
