.page-header {
    width: 100%;
    margin: 0 auto;
    position: relative;
    background-color: $header-gray;

    &__wrapper {
        width: 1440px;
        box-sizing: border-box;
        margin: 0 auto;
        padding: 80px 92px 80px 216px;
        background-image: url("../img/header-background_desktop@1x.png");
        background-repeat: no-repeat;
        background-position: right;
    }

    &__description {
        width: 380px;
        text-align: left;
    }

    &__title {
        margin: 52px 0 0;
        font-family: $Stratos;
        font-weight: normal;
        font-size: 4.56em;
        line-height: 100%;
        color: $blue;
    }

    &__about {
        margin: 32px 0 0;
        font-size: 2em;
        line-height: 130%;
        color: $blue;
    }

    &__description-note {
        margin: 56px 0 0;
        font-size: 0.89em;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 288px;

        &::before {
            display: block;
            content: '';
            min-width: 72px;
            height: 72px;
            background-image: url("../img/icon_economy.svg");
            margin: 0 16px 0 0;
        }
    }
}

@media (max-width: $tablet-max-width) {
    .page-header {
        &__wrapper {
            width: 100%;
            max-width: $tablet-max-width;
            padding: 80px 0 80px 16px;
            background-image: url("../img/header-background_tablet@1x.png");
        }

        &__description {
            width: 340px;
        }

        &__title {
            margin: 56px 0 0;
            font-size: 4em;
        }

        &__about {
            margin: 24px 0 0;
            font-size: 1.78em;
        }

        &__description-note {
            margin: 44px 0 0;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .page-header {
        &__wrapper {
            max-width: $mobile-max-width;
            padding: 56px 0 64px 16px;
            background-image: url("../img/header-background_mobile@1x.png");
        }

        &__logo {
            width: 121px;
            height: 24px;
        }

        &__description {
            width: 250px;
        }

        &__title {
            font-size: 3.5em;
        }

        &__about {
            margin: 16px 0 0;
            font-size: 1.5em;
        }

        &__description-note {
            flex-direction: column;
            align-items: flex-start;
            margin: 44px 0 0;
            width: 180px;

            &::before {
                min-width: 64px;
                height: 64px;
                margin: 0 0 16px;
                background-size: cover;
            }
        }
    }
}
