.parents {
    width: 100%;
    margin: 80px auto 0;
    position: relative;
    background-color: $parents-gray;

    &__wrapper {
        width: 1440px;
        box-sizing: border-box;
        margin: 0 auto;
        padding: 130px 65px 130px 216px;
        background-image: url("../img/parents-background_desktop@1x.png");
        background-repeat: no-repeat;
        background-position: right;
    }

    &__description {
        width: 300px;
        text-align: left;
    }

    &__title {
        margin: 0;
        font-family: $Stratos;
        font-weight: normal;
        font-size: 2.67em;
        line-height: 108%;
    }

    &__about {
        margin: 32px 0 0;
    }
}

@media (max-width: $tablet-max-width) {
    .parents {
        &__wrapper {
            width: 100%;
            max-width: $tablet-max-width;
            padding: 84px 0 84px 16px;
            background-image: url("../img/parents-background_tablet@1x.png");
        }
    }
}

@media (max-width: $mobile-max-width) {
    .parents {
        &__wrapper {
            max-width: $mobile-max-width;
            padding: 56px 16px 242px;
            background-image: url("../img/parents-background_mobile@1x.png");
            background-position: bottom;
        }

        &__description {
            width: 100%;
            max-width: $mobile-max-width;
            text-align: center;
        }
    }
}
