.page-footer {
	width: 100%;
	margin: 0 auto;
	flex: 0 0 auto;
	padding: 80px 0;

	&__wrapper {
		width: 1006px;
		margin: 0 auto;
		text-align: left;
	}

	&__description {
		margin: 0;
	}

	&__description-list {
		margin: 24px 0 0;
		padding: 0;
		list-style: none;
	}

	&__description-item {
		font-size: 0.67em;
		line-height: 150%;
		color: $black-gray;
		margin: 0 0 8px;

		&--explain {
			padding: 0 0 0 10px;
			position: relative;

			&::before {
				display: block;
				font-size: 9px;
				line-height: 10px;
				position: absolute;
				top: -4px;
				left: 0;
			}

			&:first-of-type {
				&::before {
					content: "1";
				}
			}

			&:nth-of-type(2) {
				&::before {
					content: "2";
				}
			}
		}

		&--info {
			margin: 24px 0 0;
		}
	}
}

@media (max-width: $tablet-max-width) {
	.page-footer {
		padding: 80px 16px;
		box-sizing: border-box;

		&__wrapper {
			width: 100%;
			max-width: $tablet-max-width;
		}

		&__description {
			margin: 0;
		}

		&__description-list {
			margin: 24px 0 0;
			padding: 0;
			list-style: none;
		}

		&__description-item {
			font-size: 0.67em;
			line-height: 150%;
			color: $black-gray;
			margin: 0 0 8px;

			&--explain {
				padding: 0 0 0 10px;
				position: relative;

				&::before {
					display: block;
					font-size: 9px;
					line-height: 10px;
					position: absolute;
					top: -4px;
					left: 0;
				}

				&:first-of-type {
					&::before {
						content: "1";
					}
				}

				&:nth-of-type(2) {
					&::before {
						content: "2";
					}
				}
			}

			&--info {
				margin: 24px 0 0;
			}
		}
	}
}

@media (max-width: $mobile-max-width) {
	.page-footer {
		padding: 56px 16px;

		&__wrapper {
			max-width: $mobile-max-width;
		}

		&__description-item {
			font-size: 0.75em;
		}
	}
}
