.promocode {
    width: 600px;
    margin: 48px auto 0;

    p {
        margin: 0;
    }

    a {
        text-decoration: none;
        color: $blue;

        &:hover {
            color: $light-blue;
        }
    }

    &__copy {
        width: 480px;
        margin: 32px auto 0;
        display: flex;
        justify-content: center;
    }

    &__data {
        border: 1px solid #808080;
        box-sizing: border-box;
        border-radius: 3px 0 0 3px;
        padding: 14px 40px;

        span {
            text-transform: uppercase;
            font-size: 1.33em;
            line-height: 150%;
        }
    }

    &__copy-button {
        color: $white;
        padding: 14px 48px;
        border: none;
        font-size: 1.33em;
        line-height: 150%;
        background: #2D56FC;
        border-radius: 0 3px 3px 0;
        cursor: pointer;
    }
}

@media (max-width: $tablet-max-width) {
    .promocode {
        width: 100%;
        max-width: 600px;
        margin: 32px auto 0;


        &__copy {
            width: 100%;
            max-width: 480px;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .promocode {
        width: 100%;
        max-width: $mobile-max-width;
        padding: 0 16px;
        box-sizing: border-box;
        margin: 24px auto 0;


        &__copy {
            width: 100%;
            max-width: $mobile-max-width;
            padding: 0;
            box-sizing: border-box;
        }

        &__data {
            width: 50%;
            padding: 12px 0;

            span {
                font-size: 1em;

            }
        }

        &__copy-button {
            padding: 12px 0;
            width: 50%;
            font-size: 1em;
        }
    }
}
