.economy {
    width: 1006px;
    margin: 80px auto 0;

    &__description {
        width: 686px;
        margin: 0 auto;
    }

    &__title {
        font-family: $Stratos;
        font-style: normal;
        font-weight: normal;
        font-size: 2.67em;
        line-height: 117%;
        margin: 80px 0 8px;
    }
}

@media (max-width: $tablet-max-width) {
    .economy {
        width: 100%;
        max-width: 1006px;

        &__description {
            width: 100%;
            max-width: 686px;
        }
    }
}

@media (max-width: $mobile-max-width) {
    .economy {
        width: 100%;
        max-width: $mobile-max-width;
        box-sizing: border-box;
        padding: 0 16px;

        &__description {
            width: 100%;
            max-width: $mobile-max-width;
        }

        &__title {
            font-size: 2.5em;
            line-height: 110%;
            margin: 56px 0 8px;
        }
    }
}
